.app_wrapper{
    display: grid;
    grid-template-rows: 60px 1fr 60px;
    height: 100vh;
    width: 100vw;
    background-color: #f6f6f5;
}
@media (max-width: 1000px) {
    .app_wrapper {
        grid-template-rows: 40px 1fr 40px;
    }
}
@media (max-width: 420px) {
    .app_wrapper {
        grid-template-rows: 40px 1fr 30px;
    }
}